import React, {useEffect} from "react";
import Hader from "../Hader";
import Futer from "../Futer";
import Katalog from "../Katalog";
import {Link} from "react-router-dom";
import "../../App.css"
import "../../styles/Home.css"
// @ts-ignore
import back from "../../videos/back.mp4";
import ap from "../../images/AllPrice.png";

function Home(){

    useEffect(() => {
        return () => {
            document.title = "-   ,   Godinecoffee";
        };
    }, []);

    return(
        <div>
            <Hader/>
            <div className="Content">
                <video autoPlay muted loop className="imgback">
                    <source src={back} type='video/mp4'/>
                         .
                </video>
                <div className="deviz">
                <div className="devizbol">  </div>
                    <div className="devizbol">.</div>
                    <div className="devizmal">  ,     ,  .</div>
                </div>
                <div className="contApp">
                    <Katalog type={''} katcount={6}/>
                    <br/>
                    <br/>
                    <Link to={"/buy"} className='linkHome'>
                        <img src={ap} alt="ap" className='imgbuy'/>
                          
                    </Link>
                </div>
            </div>
            <Futer/>
        </div>
    );
}

export default Home;